<template>
  <div class="container">
    <h3>Create an account</h3>
    <form>
      <div class="form-group">
        <p v-if="error" class="alert alert-danger">
          {{ error }}
        </p>
        <label for="username">Username</label>
        <input
          type="text"
          name="username"
          id="username"
          v-model="username"
          class="form-control"
        />
      </div>
      <div class="form-group">
        <label for="email">Email</label>
        <input
          type="text"
          name="email"
          id="email"
          v-model="email"
          class="form-control"
        />
      </div>
      <div class="form-group">
        <label for="password">Password</label>
        <input
          type="password"
          name="password"
          id="password"
          v-model="password"
          class="form-control"
        />
      </div>
    </form>
    <button class="btn btn-success" @click="validateUser()">Submit</button>
  </div>
</template>

<script>
import UserDataService from "../service/UserDataService";
export default {
  name: "Registration",
  data() {
    return {
      username: "",
      email: "",
      password: "",
      error: "",
    };
  },
  methods: {
    validateUser() {
      const user = {
        username: this.username,
        email: this.email,
        password: this.password,
      };
      try {
        this.errors = [];
        const res = UserDataService.validateUser(user);
        this.register(user);
      } catch (error) {
        this.error = error.message;
      }
    },
    async register(user) {
      const res = await UserDataService.createUser(user);
      if (res.message) {
        this.error = res.message;
        return;
      }
      this.$router.push({ name: "Login" });
    },
  },
};
</script>

